body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 160%;
  font-weight: 400;
  color: #ffffff;
  background-color: #7b5449;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  box-sizing: border-box;
  width: 1440px;
  margin: 0 auto;
  padding-left: 48px;
  padding-right: 32px;
}

